.OrderDetailModal {
  position: absolute;
  top: 100px;
  left: 100px;
  right: 100px;
  bottom: 150px;
  padding: 30px;
  border-radius: 10px;
  background-color: rgb(255, 255, 255);
  transition: all 1s ease-in-out;
}

.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(160, 160, 160, 0.39);
}

.tdStyle {
  font-size: 12px;
}

.trStyle {
  font-size: 12px;
}